<template>
 <div>
    <div class="main">
   
    <div class="loginFrom">
      <form id="data_from">
        <p>Login</p>
        <div class="user">
          <label>
            <!-- <span></span> -->
            <span>用户名</span>
            <input
              type="text"
              name="username"
              v-model="username"
              placeholder="  UserName"
            />
          </label>
        </div>
        <div class="password">
          <label>
            <!-- <span></span> -->
            <span>密码</span>
            <input
              type="password"
              name="password"
              v-model="password"
              placeholder="  PassWord"
            />
          </label>
        </div>
        <div id="btn" @click="log">
          <span>登录</span>
        </div>
        <div id="zc">
          <!-- <router-link to="/login/register">注册</router-link> -->
          <el-button class="login_btn" type="text" @click="dialogVisible = true"
            >注册</el-button
          >
        </div>
      </form>
      <el-dialog
        title="注册"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose"
      >
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="昵称">
            <el-input v-model="name"></el-input>
          </el-form-item>
          <el-form-item label="账号">
            <el-input v-model="User"></el-input>
          </el-form-item>
          <el-form-item label="密码">
            <el-input type="password" v-model="password"></el-input>
          </el-form-item>
          <el-form-item label="确认密码">
            <el-input type="password" v-model="password2"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="postData">注册</el-button>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
 </div>
</template>
<style>

.main{
    width: 100%;
    height: 100vh;
    background: url("../../public/img/01.jpg") 100% 100% no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loginFrom{
    width: 400px;
    height: 300px;
    background: rgba(0, 0, 0, 0.3);
    /* position: fixed; */
    margin: 0 auto;
    /* 添加内边距不影响盒子大小 */
    padding: 30px 0;
    box-sizing: border-box;
    /* 圆角 */
    border-radius: 10px;
    /* 阴影 */
    box-shadow: 0 0 5px 5px rgba(126, 88, 88, 0.4);
}
.loginFrom>form>p{
    text-align: center;
    color: white;
    font-size: 25px;
}
.loginFrom>form>div{
    width: 350px;
    height: 40px;
    margin: 0px auto 20px auto;
    color: white;
    /* background: pink; */
}
.loginFrom >form>div>label>span{
    display: inline-block;
    cursor: pointer;
    font-size: 15px;
}
.loginFrom>form>div>label>input{
    width: 100%;
    height: 25px;
    /* 透明色 */
    background: transparent;
    border: none;
    border-bottom: 1px solid white;
    outline: none;
    color: white;
}
/* 只写btn优先级不够 */
#btn{
    width: 150px;
    height: 40px;
    margin-top: 30px;
    border-radius: 50px;
    background: linear-gradient(to right ,#c979d4,#fa719d);
    text-align: center;
    line-height: 40px;
    cursor: pointer;
}
#btn:hover{
    box-shadow: 0 0 10px rgba(0,0,0,0.3) inset;
}
#zc{
    width: 100px;
    height: 30px;
    text-align: center;
    line-height: 30px;
}
#zc .login_btn{
    font-size: 15px;
    color: white;
    text-decoration: none;
}
input::-webkit-input-placeholder{
    color: #bdcada;
}

</style>
<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      username: "",
      password: "",
      dialogVisible: false,
      name: "",
      User: "",
      password: "",
      password2: "",
    };
  },
  methods: {
    log() {
      if (!this.username) {
        alert("请输入账号");
      } else {
        axios
          .post(
            "http://www.luck.com:3000/login",
            qs.stringify({
              username: this.username,
              password: this.password,
            })
          )
          .then((res) => {
            if (
              this.username == res.data.User &&
              this.password == res.data.password
            ) {
              //存储用户名session
              localStorage.setItem("usr", res.data.name);
              // 跳转
              this.$router.push("/");
            } else {
              alert(res.data);
            }
          });
      }
    },

    handleClose(done) {
      this.$confirm("确认关闭？")
        .then((_) => {
          done();
        })
        .catch((_) => {});
    },
    postData() {
      if (this.password == this.password2) {
        axios
          .post(
            "http://www.luck.com:3000/login/register",
            qs.stringify({
              name: this.name,
              User: this.User,
              password: this.password,
              password2: this.password2,
            })
          )
          .then((res) => {
            alert(res.data);
          })
          .catch((error) => {
            alert(error);
          });
      } else {
        alert("两次密码不一致！");
      }
    },
 
  },
};
</script>
