  <div class="apps">
    <header class="head">
      <h2 class="logo">DarkCode</h2>
      <input type="checkbox" id="chk" />
      <!-- 汉堡按钮 -->
      <label for="chk" class="show-menu-btn">
        <i class="fas fa-ellipsis-h"></i>
      </label>
      <ul class="menu">
        <!-- <a href="javascript:;">首页</a> -->
        <router-link to="/">首页</router-link>
        <router-link to="/Release">发布</router-link>
        <!-- <a href="javascript:;">资源下载</a> -->
        <router-link to="/Download">资源下载</router-link>
        <!-- <a href="javascript:;">视频</a> -->
        <!-- <a href="/login">登录</a> -->
        <router-link v-if="!show" to="/login">登录</router-link>
        <router-link v-if="show" to="/">欢迎:{{uname}} <button  @click="clearS">注销</button></router-link>
        <!-- 关闭图标 -->
        <label for="chk" class="hide-menu-btn">
          <i class="fas fa-times"></i>
        </label>
      </ul>
