<style>
*{
    margin: 0;
    padding: 0;
}
html,body{
  width: 100%;
  height: 100%;
    background: url("../public/img/back.jpg") no-repeat fixed;
    background-size: 100% ;
}
/* 导航 */
.head{
    width: 100%;
    height: 60px;
    background: rgba(0, 0, 0, 0.05);
    padding: 0 50px;
    color: #fff;
    box-sizing: border-box;
    z-index: 1111;
    position: fixed;
}
.logo{
    line-height: 60px;
    float: left;
}
.menu{
    float: right;
    line-height: 60px;
}
.menu a{
    color: white;
    text-decoration: none;
    text-transform: uppercase;
    padding: 0 10px;
    transition: 0.4s;
}
.menu button{
    background-color: transparent;
    border: none;
    color: white;
    font-size: 16px;
    cursor: pointer;
}
.menu button:hover{
    font-size: 18px;
}
.show-menu-btn,.hide-menu-btn{
    transition: 0.4s;
    font-size: 30px;
    cursor: pointer;
    display: none;
}
.show-menu-btn{
    float: right;
}
.show-menu-btn i{
    line-height: 60px;
}
.menu a:hover,.show-menu-btn:hover,.hide-menu-btn:hover{
    color: #3498db;
}
#chk{
    position: absolute;
    visibility: hidden;
    z-index: -1000;
}

/* 导航响应式 */
@media screen and (max-width:800px){
    .show-menu-btn,.hide-menu-btn{
        display: block;
    }
    .menu{
        position: fixed;
        width: 100%;
        height: 100vh;
        background: #333;
        right: -100%;
        top: 0;
        text-align: center;
        padding: 80px 0;
        line-height: normal;
        z-index: 1000;
    }
    .menu a{
        display: block;
        padding: 20px;
    }
    .hide-menu-btn{
        position: absolute;
        top: 40px;
        right: 40px;
    }
    #chk:checked ~.menu{
        right: 0;
    }
}
</style>
<script>
import foot from "./views/footer.vue"
export default {
  components:{foot:foot},
  data(){
    return{
      uname:localStorage.getItem("usr"),
      show:localStorage.getItem("usr"),
    }
  },
  methods:{
    clearS(){
      localStorage.clear();
      this.show=false
      this.uname=""
      this.$router.push("/")
    }
  },
  watch:{
    '$route.path':function(){
      this.uname=localStorage.getItem("usr")
      this.show=localStorage.getItem("usr")
 
    }
  },
  mounted: function () {
    var head = document.querySelector(".head");
    // 页面滚动事件
    document.addEventListener("scroll", function () {
      console.log("gbfueb");
      if (window.pageYOffset > 0) {
        head.style.background = 'linear-gradient(to right, #3a95f0,#33fde2)';
      } else {
        head.style.background = "rgba(0, 0, 0, 0.05)";
      }
    });
  },

  created(){
   
  }
};
</script>


