<template>
  <div>
    <!-- 占位元素 -->
    <div class="head_zw"></div>
    <div class="details_box" v-for="(item, index) in list" :key="index">
      <!-- 标题 -->
      <h1 class="details_h1" v-html="item.category"></h1>
      <!-- 内容区 -->
      <div class="details_box1" v-html="item.content"></div>
      <hr />
      <!-- 评论区 -->
      <div class="de_txt">
        <!-- 发布评论 -->
        <div class="de_txt_box1">
          <!-- <input type="textarea"> -->
          <textarea placeholder="请文明发言~" v-model="data_form"></textarea>
          <div class="txt_btn" @click="commBtn">发布</div>
        </div>
        <!-- 评论显示 -->
        <div class="de_txt_box2">
          <!-- <h1>dq</h1> -->
          <div v-for="(i,inde) in commList" :key="inde">
            <!-- 发布者 -->
            <h4>{{i.cName}}</h4>
            <!-- 发布时间 -->
            <p>{{i.created_at}}</p>
            <!-- 内容 -->
            <table>
              {{i.cInfo}}
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
.head_zw {
  width: 100%;
  height: 60px;
  background: linear-gradient(to right, #3a95f0,#33fde2);
  margin-bottom: 15px;
}
.details_box {
  width: 750px;
  background: white;
  border-radius: 5px;
  margin: 0px auto 15px auto;
  padding: 40px 0 40px 0;
}
/* 标题 */
.details_h1 {
  text-align: center;
  padding: 20px 0 20px 0;
}
.details_box1 {
  padding: 20px 100px 20px 100px;
}
.de_txt {
  width: 100%;
}
.de_txt_box1 {
  width: 100%;
  padding: 0 100px 0 100px;
  margin: 10px 0 10px 0;
  box-sizing: border-box;
}
/* 输入评论 */
.de_txt_box1 textarea {
  border: 1px solid darkgray;
  width: 100%;
  height: 100px;
  border-radius: 1.5px;
  margin-bottom: 10px;
  padding: 10px;
  font-style: 13px;
}
/* 发布评论按钮 */
.de_txt_box1 .txt_btn {
  width: 100px;
  height: 23px;
  border-radius: 2px;
  text-align: center;
  line-height: 23px;
  font-size: 12px;
  font-family: "微软雅黑";
  font-weight: bold;
  background: rgb(228, 228, 228);
  cursor: pointer;
}
/* 显示评论 */
.de_txt_box2 {
  width: 100%;
  padding: 10px 100px 10px 100px;
  margin: 10px 0 10px 0;
  box-sizing: border-box;
}
.de_txt_box2 div {
  width: 100%;
  /* height: 70px; */
  border-bottom: 1px solid darkgray;
  /* background: darkgreen; */
  padding: 7px;
  margin: 7px 0 7px 0;
}
.de_txt_box2 div p {
  font-size: 10px;
  color: darkgrey;
  padding: 5px 0px 5px 5px;
}
.de_txt_box2 div table {
  width: 80%;
  font-size: 13px;
}
</style>
<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      list: [],
      data_form: "",
      cNmae: localStorage.getItem("usr"),
      commList:[],
    };
  },
  methods: {
    // 查询文章
    getDtae() {
      axios
        .get(`http://www.Luck.com:3000/queryDe/${this.$route.params.id}`)
        .then((res) => {
          this.list = res.data;
        });
    },
    // 添加评论
    commBtn() {
      axios
        .post("http://www.luck.com:3000/comment",
          qs.stringify({
            cName: this.cNmae,
            cInfo: this.data_form,
            com_id:this.$route.params.id
          })
        )
        .then((res) => {
          this.commData();
          this.data_form="";
          alert(res.data);
        });
    },
    // 查询评论
    commData(){
        axios.get(`http://www.luck.com:3000/commData/${this.$route.params.id}`).then(res => {
          this.commList=res.data;
        })
    }
  },
  created() {
    this.getDtae();
    this.commData();
  },
};
</script>