<template>
  <div>
    <!-- 占位元素 -->
    <div class="head_zw"></div>
    <div class="box">
      <h1>文章发布</h1>
      <div class="box1">
        <input v-model="cat" type="text" name="cat" placeholder="请输入标题" />
      </div>
      <div class="box1">
        <!-- <input v-model="tit" type="text" name="tit" :placeholder="name" /> -->
        <p>发布人:{{name}}</p>
      </div>
      <div class="quill-wrap">
        <quill-editor
          ref="myQuillEditor"
          v-model="content"
          :options="editorOption"
        />
      </div>
      <!-- <button @click="add">发布</button> -->
      <div class="btn" @click="add">发布</div>
      <!-- <p>ghjkl</p> -->
    </div>
  </div>
</template>

<script>
import "quill/dist/quill.snow.css";
import { quillEditor, Quill } from "vue-quill-editor";
import { container, ImageExtend, QuillWatch } from "quill-image-extend-module";
Quill.register("modules/ImageExtend", ImageExtend);

import axios from "axios";
import qs from "qs";

export default {
  components: { quillEditor, },
  data() {
    return {
      name:localStorage.getItem("usr"),
      content: "",
      cat: "",
      // tit: "",
      editorOption: {
        // 富文本框参数设置
        modules: {
          ImageExtend: {
            loading: true,
            name: "img",
            action: "http://127.0.0.1:7001/yun/upload",
            response: (res) => {
              return `http://127.0.0.1:7001/public/uploadfile/${res.data.filename}`;
            },
          },
          toolbar: {
            container: container,
            handlers: {
              image: function () {
                QuillWatch.emit(this.quill.id);
              },
            },
          },
        },
      },
    };
  },
  methods: {
    add() {
      axios
        .post(
          "http://www.luck.com:3000/rel",
          qs.stringify({
            rel: this.content,
            cat: this.cat,
            tit: this.name,
          })
        )
        .then((res) => {
          alert(res.data);
        });
    },
  },
};
</script>
<style scoped>
.head_zw {
  height: 60px;
  width: 100%;
  background: linear-gradient(to right, #3a95f0,#33fde2);
  margin-bottom: 15px;
}
.ql-container {
  min-height: 300px;
}
.quill-wrap{
  height: 300px;
}




.box{
  background: white;
  width: 750px;
  margin: 0 auto;
  padding: 40px;
  box-sizing: border-box;
  border-radius: 5px;
  margin-bottom: 15px;
}
.box h1{
  color: #3a95f0;
  margin-bottom: 30px;
}
.box1{
  margin-bottom: 20px;
}
.box1 input{
  border: none;
  width: 100%;
  height: 35px;
  /* background: transparent; */
  outline: none;
  border-bottom: 1px solid black;
}
.btn{
  width: 150px;
  height: 40px;
  margin-top: 20px;
    /* background: linear-gradient(to right ,#c979d4,#fa719d); */
  background:linear-gradient(to right, #c979d4,#fa719d);
  text-align: center;
  line-height: 40px;
  border-radius: 20px;
  color: white;
  cursor: pointer;
  z-index: 999;
}
</style>