<script>
// 引入axios
import axios from "axios";
export default {
  data() {
    return {
      addList: [],
      activeNames: ["1"],
    };
  },
  methods: {
    handleChange(val) {
      console.log(val);
    },
    //初始化查询
    getFruitlist() {
      axios.get("http://www.luck.com:3000/query").then((res) => {
        // console.log(res.data);
        this.addList = res.data;
      });
    },
  },
  created() {
    this.getFruitlist();
  },
  mounted: function () {
    // 动画函数
    function animator(obj, target, callback) {
      clearInterval(obj.timer);
      obj.timer = setInterval(function () {
        if (obj.offsetLeft == target) {
          clearInterval(obj.timer);
          if (callback) {
            callback();
          }
        }
        // 缓动步长，记得取整
        var step = (target - obj.offsetLeft) / 10;
        step = step > 0 ? Math.ceil(step) : Math.floor(step);
        obj.style.left = obj.offsetLeft + step + "px";
      }, 7);
    }

    var lunbo = document.querySelector(".lunbo");
    var btn1 = document.querySelector(".lef");
    var btn2 = document.querySelector(".rig");
    var ul = document.querySelector(".uls");
    var imgs = document.querySelector(".uls").querySelector("img");
    var lis = document.querySelector(".uls").querySelectorAll("li");

    lunbo.addEventListener("mouseenter", function () {
      btn1.style.display = "block";
      btn2.style.display = "block";
      // clearInterval(lbitem);
      // lbitem = null;
    });
    lunbo.addEventListener("mouseleave", function () {
      btn1.style.display = "none";
      btn2.style.display = "none";
      // lbitem = setInterval(function () {
      //   btn2.click();
      // }, 3000);
    });
    var num = 0;
    var first = lis[0].cloneNode(true);
    ul.appendChild(first);
    btn1.addEventListener("click", function () {
      if (num == 0) {
        num = lis.length;
        imgs.style.left = -num * imgs.offsetWidth + "px";
      }
      num--;
      animator(ul, -num * imgs.offsetWidth);
      console.log(12);
    });
    btn2.addEventListener("click", function () {
      if (num == lis.length) {
        imgs.style.left = 0;
        num = 0;
      }
      num++;
      animator(ul, -num * imgs.offsetWidth);
    });
    // 自动播放动画
    // var lbitem = setInterval(function () {
    //   btn2.click();
    // }, 3000);
  },
};
</script>